.LeaveComment{
	position: fixed;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.6);
	top: 0;
	bottom: 0;
	left: 0;
	z-index: 4;
	color: #fff;
	padding: 10px;
	box-sizing: border-box;
	display: none;
}
.close{
	position: absolute;
	top: 50px;
	right: 8%;
	width: 24px;
	height: 24px;
	padding: 4px;
	background-color: #fa5757;
	cursor: pointer;
	line-height: 22px;
}
.comment-container{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
}
.comment-textarea{
	min-width: 80%;
	max-width: 80%;
	min-height: 400px;
	max-height: 400px;
	border-style: none;
	border-radius: 20px;
	padding: 20px;
	overflow: hidden;
	word-break: break-word;
	line-break: anywhere;
	font-family: Rubik,sans-serif;
}
.comment-textarea:focus-visible{
	outline: 1px solid #7284da;
}
.comment-input{
	min-width: 80%;
	max-width: 80%;
	border-style: none;
	border-radius: 20px;
	padding: 20px;
	overflow: hidden;
	word-break: break-word;
	line-break: anywhere;
	font-family: Rubik,sans-serif;
	margin-bottom: 10px;
}
.comment-input:focus-visible{
	outline: 2px solid #7284da;
}
.comment-button{
	display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    line-height: 21px;
    color: #fff;
    font-family: Rubik,sans-serif;
    font-weight: 400;
    height: 60px;
    width: 210px;
    border-radius: 10px;
    border: none;
    background: linear-gradient(93.78deg,#7284da 18.17%,#4947cc 92.38%);
    cursor: pointer;
    margin-top: 20px;
}