.Home{
	width: 100%;
	display: flex;
	flex-direction: column;
}
.Exchange-wrapper{
	font-size: 14px;
	line-height: 1.42857143;
	box-sizing: border-box;
	width: 100%;
	padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
/*	display: flex;
	justify-content: center;
	align-items: center;*/
	position: relative;
}



.statick-block{
	display: block;
/*	max-width: calc(60% - 35px);*/
    width: 100%;
    margin-top: 30px;
    margin-bottom: 80px;
    box-sizing: border-box;
}
.statick-list{
	padding: 0;
	margin: 0;
	margin-bottom: 36px;
	display: flex;
	flex-wrap: nowrap;
	justify-content: space-evenly;
	list-style: none;   
	box-sizing: border-box;
	width: 100%;
}
.item{
	display: flex;
    margin-left: 10px;
    margin-top: 10px;
    box-sizing: border-box;
    height: 105px;
}
.icon{
	display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 47px;
    height: 47px;
    border-radius: 10px;
    background-color: #e6f2ff;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.text-block{
	text-align: left;
}
.main-text{
	color: #2c2c2c;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
}
.sub-text{
	padding-left: 30px;
	font-size: 14px;
    line-height: 17px;
    color: silver;
}
.white-block{
	background: #fff;
    border-radius: 10px;
    box-sizing: border-box;
    margin-bottom: 80px;
    width: 100%;
    padding: 30px 30px 30px 30px;
    max-width: calc(60% - 39px);
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.white-block-header{
	margin: 1px;
	background-color: #ffffff;
	border-radius: 10px;
	margin-bottom: 20px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.white-block-title{
	margin: 0;
}
.white-block-button{
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 18px;
    line-height: 21px;
    color: #fff;
    font-family: Rubik,sans-serif;
    font-weight: 400;
    height: 60px;
    width: 210px;
    border-radius: 10px;
    color: #1787fb;
    background: rgba(23,135,251,.1);
    border: 1px solid rgba(23,135,251,.2);
    max-width: 190px;
    max-height: 50px;
}
.white-block-button:hover{
	color: #1787fb;
    background: rgba(23,135,251,.2);
}
.white-block-button:active{
	color: #1787fb;
    background: rgba(23,135,251,.5);
}
.testimonials-list{
	list-style: none;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.testimonial-item{
	background: #f4f4f4;
    border-radius: 10px;
    margin-bottom: 20px;
    padding: 20px;
    position: relative;
}
.testimonial-item:before{
	content: '';
    width: 0;
    height: 0;
    border-top: 14px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 14px solid #f4f4f4;
    position: absolute;
    bottom: 0;
    left: -8px;
}
.testimonial-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.testimonial-title{
	font-size: 14px;
    margin: 0;
    color: #2c2c2c;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
}
.date{
	color: silver;
    font-size: 12px;
    line-height: 14px;
    margin: 0;
}
.testimonial-text{
	font-size: 16px;
    margin: 0;
    text-align: left;
}
.block-title{
	text-align: left;
}
.service-block{
	margin-bottom: 80px;
}
.service-list{
	display: flex;
	max-width: calc(60% - 35px);
	justify-content: space-between;
}
.service{
	margin-bottom: 10px;
	min-width: 33%;
	max-width: 33%;
	text-align: left;
	max-width: 200px;
}
.service-title{
	color: #1787fb;
	font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
}
.svg-icon{
	border-radius: 10px;
    background: #f4f4f4;;
    display: flex;
    width: 75px;
    height: 75px;
    justify-content: center;
    align-items: center;
}
.in-work{
	pointer-events: none;
	filter: blur(3px);
}

.one-sides{
	background-color: #fff;
    padding: 80px 0;
    margin-top: 1px;
}
.advantages-container{
	margin-right: auto;
    margin-left: auto;
    max-width: 100%;
}
.advantages{
	display: flex;
	justify-content: space-between;
	flex-direction: row;
}
.advantage-item{
	max-width: 50%;
    margin-bottom: 20px;
    padding-left: 30px;
    padding-right: 30px;
    word-break: break-word;
}
.icon-block{
	display: flex;
    justify-content: flex-start;
    align-items: center;
}
.icon-block-image{
	vertical-align: middle;
	width: 200px;
	height: 140px;
	background-repeat: no-repeat;
	background-position: center;
	background-position: center;
}
.image-excha{
	background-image: url("../images/excha.webp");
}
.image-otc{
	background-image: url("../images/otc.webp");
}
.image-p2p{
	background-image: url("../images/p2p.webp");
}
.quote{
	font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    padding-left: 40px;
    position: relative;
}
.quote:before{
	content: '';
    display: block;
    width: 30px;
    height: 30px;
    top: 0;
    left: 0;
    position: absolute;
    background-color: #5bd158;
    -webkit-mask: url(../images/quote.svg);
    -webkit-mask-repeat: no-repeat;
}
.advantage-list{
	list-style: none;
    padding: 0;
    margin: 0;
}
.advantage-list>li{
	position: relative;
}
.advantage-item-header{
	font-weight: 600;
	font-size: 18px;
}
.advantage-list-item::before{
	content: '';
    display: block;
    width: 5px;
    height: 5px;
    top: 5px;
    left: -20px;
    position: absolute;
    border-radius: 100%;
    background-color: #1787fb;
}
.advantage-list-item-message::before{
	content: '';
    display: block;
    width: 19px;
    height: 19px;
    top: 0;
    left: -25px;
    position: absolute;
    background-color: #1787fb;
    -webkit-mask: url(../images/message.svg);
    -webkit-mask-repeat: no-repeat;
}
.advantage-list-item-dollar::before{
	content: '';
    display: block;
    width: 19px;
    height: 25px;
    top: 0;
    left: -25px;
    position: absolute;
    background-color: #1787fb;
    -webkit-mask: url(../images/dollar.svg);
    -webkit-mask-repeat: no-repeat;
}
@media only screen and (max-width: 760px){
	.Exchange-wrapper{
		padding-left: 0;
		padding-right: 0;
	}
	.statick-block{
		display: flex;
		flex-direction: row;
		width: 100%;
	}
	.statick-list{
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: stretch;
	}
	.item{
		justify-content: flex-start;
	}
	.icon{
		min-width: 47px;
		max-width: 30%;
	}
	.white-block{
		max-width: 90%;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 80px;
	}
	.white-block-header{
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.white-block-title{
		margin-bottom: 15px;
	}
	.block-title{
		text-align: center;
	}
	.service-list{
		max-width: 100%;
		flex-direction: column;
	}
	.service{
		margin-bottom: 20px;
	}
	.service-content{
		display: flex;
		flex-direction: row;
		align-items: center;
	}
	.svg-icon{
		min-width: 75px;
		min-height: 75px;
		margin-right: 30px;
	}
	.advantages{
		flex-direction: column;
	}
	.advantage-item{
		max-width: 100%;
	}
	.icon{
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.icon-block{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

}
@media (min-width: 760px) and (max-width: 992px){
	.statick-block{
		display: flex;
		flex-direction: column;;
		max-width: 100%;
	}
	.statick-list{
		height: 100px;
		display: flex;
		flex-direction: row;
    	justify-content: space-evenly;
    	align-items: stretch;
	}
	.item{
		justify-content: flex-start;
		width: 100%;
	}
	.icon{
		min-width: 47px;
	}
	.white-block{
		max-width: 80%;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 80px;
	}
	.block-title{
		text-align: center;
	}
	.service-list{
		max-width: 100%;
		flex-direction: row;
	}
	.service{
		max-width: 30%;
	}
	.advantages{
		flex-direction: column;
	}
	.advantage-item{
		max-width: 100%;
	}
	.icon{
		display: flex;
		justify-content: center;
		align-items: center;
	}
}

@media (min-width: 992px){
	.Exchange-wrapper{
		width: 970px;
		flex-direction: column;
	}
	.statick-block{
		display: flex;
		flex-direction: row;
		max-width: calc(60% - 35px);
	}
	.statick-list{
		width: 100%;
		height: 315px;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
	}
	.item{
		height: 100px;
		width: 100%;
		align-items: flex-start;
	}
	.icon{
		min-width: 47px;
	}
	.block-title{
		text-align: left;
	}
	.service{
		margin: 2px;
	}
	.service-list{
		flex-direction: row;
	}
	.service-content{
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.advantages{
		flex-direction: column;
	}
	.advantage-item{
		max-width: 100%;
	}
}

@media (min-width: 1200px){
	.Exchange-wrapper {
    	width: 1170px;
    }
   	.statick-block{
		display: block;
		max-width: calc(60% - 35px);
	}
	.statick-list{
		height: 100px;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: flex-start;
	}
	.white-block{
		max-width: calc(60% - 39px);
	}
	.service-list{
		flex-direction: row;
	}
	.service{
		margin-bottom: 10px;
		max-width: 30%;
		justify-content: flex-start;
	}
	.service-block{
		margin-bottom: 80px;
	}
	.advantages{
		flex-direction: row;
	}
	.advantage-item{
		max-width: 50%;
	}
}