.ticker-wrapper{
	background-color: rgba(255, 255, 255, 0.5);
	color: white;
	width: 100%;
	overflow: hidden;
	height: 30px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-around;
	font-size: 12px;
	margin-top: 5px;
}


.ticker__first-half, .ticker__second-half{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  transform: translate(100%, 0);
  animation: 50s ticker infinite linear forwards;
}
.ticker__second-half{
  transform: translate(100%, 0);
  animation: 50s ticker 25s infinite linear forwards;
}

.ticker__element{
  margin-left: 15px;
  font-size: 14px;
}
.ticker__element>a{
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  text-decoration: none;
  font-family: Rubik, sans-serif;
}
.name{
  margin-right: 2px;
}

.rate-up{
  margin-left: 2px;
  display: flex;
  align-items: center;
  color: #26cc6a;;
}
.rate-up:before{
  content: '\25b2';
    width: 12px;
    display: inline-block;
}

.rate-down{
  margin-left: 2px;
  display: flex;
  align-items: center;
  color: red;
}
.rate-down:before{
  content: '\25BC';
    width: 12px;
    display: inline-block;
}

@keyframes ticker {
  0% {
    transform: translate(100%, 0);
  }

  50% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-100%, 0);
  }
}