.Exchange{
	text-align: left;
	display: flex;
	flex-wrap: nowrap;
	align-items: flex-start;
	width: 100%;
}
.Exchange__left-block{
	display: flex;
	justify-content: center;
	align-items: center;
	align-content: flex-start;
	max-width: 60%;
	width: 100%;
	margin: 1px;
	z-index: 1;
}
.currency-sell{
	width: 50%;
}
.currency-buy{
	width: 50%;	
}
.currency-list{
	list-style: none;
	text-align: left;
	padding: 0;
	margin: 0;
}
.list-header{
	margin: 1px;
	padding: 14px 0;
	background-color: #ffffff;
}
.title{
	margin: 0;
	text-align: left;
	padding-left: 30px;
	line-height: 20px;
}
.title-remove-padding{
	padding-left: 0;
}
.list-item{
	background-color: #ffffff;
	height: 45px;
	box-sizing: border-box;
	margin: 1px;
	padding: 0px 30px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.list-item>div{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: 80%;
}
.list-item>div>img{
	margin-right: 20px;
}
.list-item>p{
	margin: 0;
}

.Exchange__right-block{
	max-width: 40%;
	width: 100%;
	background-color: #fff;
	margin: 2px;
	margin-left: 0px;
	z-index: 1;
}
.Exchange__right-block:after{
	content: '';
    background-color: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}
.preorder-block{
	max-width: 100%;
	margin-left: 0;
    padding: 50px;
    position: relative;
}
.preorder-course{
	font-weight: 600;
}
.allerts-area{
	display: none;
	flex-direction: column;
	align-items: flex-start;
	box-sizing: border-box;
	border: 1px solid #fb2f2f;
	padding: 15px;
	font-size: 14px;
	margin: 20px 0;
}
.allert-message{
	display: none;
	margin: 0;
}
.currency-header{
	display: flex;
	align-items: flex-end;
	position: relative;
	margin: 10px 0;
}
.currency-img{
	width: 40px;
	height: 40px;
}
.hint-block{
	position: absolute;
	top: 0;
	font-size: 14px;
	color: lightgray;
	font-weight: 500;
}
.form-control{
	background-color: inherit;
    border-radius: 0;
    border: 0;
    border-bottom: 1px solid #f0f0f0;
    -webkit-box-shadow: none;
    box-shadow: none;
    font-size: 18px;
    line-height: 21px;
    height: 50px;
    max-width: 420px;
    width: 100%;
    padding-left: 0;
    color: #555;
}
.form-control:focus-visible{
	outline: none;
	border-bottom: 1px solid #adc7e6;
}
.buy-form-wallet{
	margin-top: 10px;
	display: flex;
	flex-direction: column;
}
.buy-wallet-label{
	font-size: 14px;
	color: lightgray;
	font-weight: 500;
}
.buy-form-mail{
	margin-top: 10px;
	display: flex;
	flex-direction: column;
}
.buy-mail-label{
	font-size: 14px;
	color: lightgray;
	font-weight: 500;
}
.button-block{
	margin-top: 30px;
}
.form-submit{
	display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    line-height: 21px;
    color: #fff;
    font-family: Rubik,sans-serif;
    font-weight: 400;
    height: 60px;
    width: 210px;
    border-radius: 10px;
    border: none;
    background: linear-gradient(93.78deg,#74da72 18.17%,#4acc47 92.38%);
    cursor: pointer;
}
.form-submit:hover{
	color: #fff;
    background: #4acc47;
}
.form-submit:active{
	background: #4acc47;
}
.active{
	background-color: #e4f1fe;
}
.rules-block{
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-end;
}
.rules-checkbox{
	width: 25px;
	height: 25px;
	cursor: pointer;
	margin-right: 15px;
}
.rules-text>a{
	color: #1787fb;
	text-decoration: underline;
}



@media only screen and (max-width: 479px){
	.preorder-block{
		padding: 30px;
	}
}

@media(max-width: 768px){
	.Exchange{
		justify-content: center;
		align-items: center;
	}
	.Exchange__left-block{
		max-width: 100%;
		flex-direction: column;
	}
	.Exchange__right-block{
		max-width: 100%;
		margin-left: 2px;
	}
	.currency-sell{
		width: 100%;
	}
	.currency-buy{
		width: 100%;
	}
}

@media (max-width: 992px){
	.Exchange{
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	.Exchange__left-block{
		max-width: 100%;
	}
	.Exchange__right-block{
		max-width: 100%;
		margin-left: 2px;
	}
	.Exchange__right-block:after{
		display: none;
	}
	.Exchange-block{
		width: 100%;
	}
}