.Footer{
	background-color: #2c2c2c;
	color: #fff;
}
.footer-header{
	position: relative;
    padding-top: 60px;
}
.footer-header::after{
	content: '';
    display: block;
    width: 100px;
    height: 3px;
    margin: 50px auto;
    background-color: #1787fb;
    -webkit-box-shadow: 0 4px 20px #1787fb;
    box-shadow: 0 4px 20px #1787fb;
}
.footer-bottom{
	background-color: #2f2f2f;
	padding-bottom: 7px;
}
.footer-content>p{
    margin-bottom: 0;
    padding-bottom: 5px;
}