@font-face{
	font-family: "Helvetica Neue";
	src: url("../fonts/Helvetica Neue Condensed Bold.ttf");
}
.Offer{
	display: flex;
	width: 100%;
	margin: 0 5px 0 5px;
	padding: 0 20px 0 20px;
	box-sizing: border-box;
}
p{
	font-size: 14px;
}
.container{
	position: relative;
	width: 100%;
	padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    text-align: left;
    display: flex;
}
.left_block{
	width: 60%;
	word-wrap: break-word;
    word-break: break-all;
}
.order-info{
	margin-right: 39px;
}
.order-info>P{
	font-size: 14px;
	word-wrap: break-word;
	word-break: normal;
}
.right_block{
	background-color: #fff;
	width: 40%;
	font-size: 14px;
	margin-top: 1px;
	padding-left: 30px;
	z-index: 1;
}
.right_block:after{
	content: '';
    background-color: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}
.page-title{
	font-weight: 600;
    font-size: 40px;
    line-height: 47px;
    margin-bottom: 30px;
}
.buy-form-mail{
	margin-top: 10px;
	display: flex;
	flex-direction: column;
}
.buy-mail-label{
	font-size: 14px;
	color: lightgray;
	font-weight: 500;
}
.form-control{
	background-color: inherit;
    border-radius: 0;
    border: 0;
    border-bottom: 1px solid #f0f0f0;
    box-shadow: none;
    font-size: 18px;
    line-height: 21px;
    height: 50px;
    max-width: 420px;
    width: 100%;
    padding-left: 0;
    color: #555;
    margin-bottom: 20px;
}
.form-control:focus-visible{
	outline: none;
	border-bottom: 1px solid #adc7e6;
}
.offer-currency-header{
	display: flex;
    align-items: center;
    margin: 10px 0;
}
.header-image{
	width: 30px;
	height: 30px;
	margin-right: 15px;
}
.form-item{
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.form-item>p:nth-child(1){
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 30%;
}
.form-item>p:nth-child(2){
	display: flex;
	justify-content: flex-end;
	font-weight: 600;
	font-size: 18px;
    line-height: 21px;
    width: 70%;
    word-wrap: break-word;
    word-break: break-all;
}
.offer-title{
	color: #2c2c2c;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    padding-left: 0;

}
.item-title{
	font-size: 14px;
    line-height: 17px;
    font-weight: 400;
    color: #9a9a9a;
    width: 140px;
}
.alert-block{
	background-color: #fff;
    color: #000;
    text-align: left;
    font-size: 14px;
    line-height: 150%;
    border: 2px solid red;
    box-sizing: border-box;
    padding: 15px;
    margin-bottom: 20px;
    word-wrap: break-word;
    word-break: normal;
}
.alert-block-yellow{
	background-color: #fff;
    color: #000;
    text-align: left;
    font-size: 14px;
    line-height: 150%;
    border: 2px solid #faebcc;;
    box-sizing: border-box;
    padding: 15px;
    margin-bottom: 20px;
    word-wrap: break-word;
    word-break: normal;
}
.walletQR{
	border: 20px solid #ffffff;
}
.centered{
	margin-top: 30px;
	justify-content: space-around;
}
.paid-button{
	display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    line-height: 21px;
    color: #fff;
    font-family: Rubik,sans-serif;
    font-weight: 400;
    height: 60px;
    width: 210px;
    border-radius: 10px;
    border: none;
    background: linear-gradient(93.78deg,#74da72 18.17%,#4acc47 92.38%);
    cursor: pointer;
}
.paid-button:hover{
	color: #fff;
    background: #4acc47;
}

#offerStatus{
	word-break: break-word;
}

.green{
	color: #26cc6a;
}
.fs-14{
	font-size: 14px;
}



@media (min-width: 1200px){
	.container {
    	width: 1170px;
	}
}

@media (max-width: 992px){
	.container {
    	width: 970px;
	}
	.page-title{
    	font-size: 30px;
    	line-height: 36px;
	}	
}

@media(max-width: 768px){
	.Offer{
		padding: 0;
	}
	.container{
		width: 100%;
		padding: 10px;
	}
	.offer-container{
		padding-right: 0;
		padding-left: 5px;
		width: 100%;
		flex-direction: column;
		margin: 0;
	}
	.left_block{
		width: 100%;
	}
	.right_block{
		box-sizing: border-box;
		width: 100%;
		padding-left: 20px;
		padding-right: 20px;
		padding-bottom: 20px;
		margin-bottom: 10px;
	}
	.right_block::after{
		display: none;
		content: none;
	}
	.page-title{
    	font-size: 24px;
    	line-height: 28px;
	}	
}