.navbar{
	background-color: #282c34;
	min-height: 140px;
	width: 100%;
	padding-top: 15px;
  	display: flex;
  	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}

.navbar__row{
	height: 100%;
	width: 100%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-direction: row;
}
.navbar__logo{
	width: 40%;
}
.navbar-logo{
	max-width: 200px;
	max-height: 200px;
	min-height: 200px;
	min-width: 200px;
}
.navbar__logo>a{
	color: white;
	font-size: 26px;
}

.navbar__links{
	margin: 0;
	padding: 0;
	width: 60%;
	list-style: none;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
}
.navbar__links>li{
	width: 100%;
	height: 100px;
	box-sizing: border-box;
	border-right: 1px solid #434957;
	width: 100%;
	border-radius: 0px;
}

.navbar__links>li:hover{
	text-shadow: 0px 0px 5px #e4f1fe;
}

.navbar__links-row2{
	border-top: 1px solid #434957;
	margin: 0;
	padding: 0;
	width: 100%;
	list-style: none;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
}
.navbar__links-row2>li{
	width: 100%;
	height: 50px;
	box-sizing: border-box;
	border-right: 1px solid #434957;
	width: 100%;
	border-radius: 0px;
}


.navbar__link{
	text-decoration: none;
	color: #ffffff;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}
.navbar__info{
	color: #fff;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	height: 100%;
	word-break: break-word;
	text-shadow: none;
}
.navbar__info:hover{
	text-shadow: none;
}
.navbar__info>h5{
	padding: 0;
	margin: 0;
}
.tech-support{
	color: #1787fb;
	text-decoration: underline;
}

@media only screen and (max-width: 479px){
	.navbar__logo{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
	}
	.navbar__row{
		flex-direction: column;
	}

	.navbar__row .navbar__links{
		width: 100%;
	}
}

@media(max-width: 768px){
	.navbar__logo{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 60%;
	}
	.navbar__row{
		flex-direction: column;
	}

	.navbar__row .navbar__links{
		width: 100%;
	}
}