.App {
  text-align: center;
  position: relative;
}

.App-section {
  background-color: #e6e6e6;
/*  min-height: 100vh;*/
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  overflow-x: hidden;
  overflow-y: hidden;
}

.App-link {
  color: #61dafb;
}

.App-link:hover{
  cursor: pointer;
  text-decoration: underline;
}
.support-block{
  box-sizing: border-box;
  position: fixed;
  bottom: 3%;
  right: -87px;
  z-index: 3;
  width: 160px;
  display: flex;
  justify-content: space-around;
  padding-right: 10px;
}
.support-block:hover{
  justify-content: flex-start;
}
.support-block:hover .support-block-caller{
  display: none;
}
.support-block-caller{
  margin-right: 10px;
  background-color: #e6f2ff;
  height: 70px;
  width: 70px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50%;
  background-image: url("./images/message.svg");
  box-sizing: border-box;
  border-radius: 10px;
}
.static-support{
  box-sizing: border-box;
  width: 70px;
  height: 70px;
  border-radius: 100%;
  box-shadow: 0px 0px 3px black;
}

@media(max-width: 768px){
  .support-block-caller{
    display: none;
  }
  .support-block{
    right: 0;
    width: 70px;
  }
  .static-support{
    width: 60px;
    height: 60px;
  }
}